/* Type utilities */
.h0 {
  font-size: $h0-mobile;
  line-height: 1.0125;
  margin-top: 0.85em;
  word-wrap: break-word;

  @media (min-width: $breakpoint-lg) {
    font-size: $h0-desktop;
  }
}

.text-right {
  text-align: right;
}

.no-underline,
.no-underline-hover {
  text-decoration: none;
  border-bottom: none;
}

.no-underline-hover {
  &:hover {
    text-decoration: none !important;
    border-bottom: none !important;
  }
}

// Link styles for navigation
.link-primary {
  font-weight: bold;
  text-decoration: none;
  border-bottom: 2px solid currentColor;
  padding-bottom: 1px;

  &:hover {
    color: var(--color-primary-link);
    text-decoration: none;
    border-bottom: 2px dotted;
  }
}

.bold {
  font-weight: bold;
}
.italic {
  font-style: italic;
}
.uppercase {
  text-transform: uppercase;
}

.lh-condensed {
  line-height: 1.25;
}

.list-reset {
  list-style: none;
  padding-left: 0;
}

/* Border utilities */
.border-bottom-thick {
  border-bottom: 2px solid;
  border-color: currentColor;
}
.border-bottom-thin {
  border-bottom: 1px solid;
  border-color: currentColor;
}
.border-top-thick {
  border-top: 2px solid;
  border-color: currentColor;
}
.border-top-thin {
  border-top: 1px solid;
  border-color: currentColor;
}
.border-0 {
  border: 0;
}

/* Theme color utilities */
.header-background {
  background-color: var(--color-background);
}
.header-border {
  border-color: var(--color-foreground);
}
.header-title {
  color: var(--color-title);
}
.header-text {
  color: var(--color-foreground);
}
.header-social {
  fill: var(--color-foreground);
}
.header-link:hover {
  color: var(--color-nav-link) !important;
} // used for navigation links on homepage
.text-accent {
  color: var(---color-text-accent);
} // used for date in post list and home link

/* Layout utilities */
.container {
  max-width: $container-width;
}

.col-1 {
  width: (1 / 12 * 100%);
}
.col-2 {
  width: (2 / 12 * 100%);
}
.col-3 {
  width: (3 / 12 * 100%);
}
.col-4 {
  width: (4 / 12 * 100%);
}
.col-5 {
  width: (5 / 12 * 100%);
}
.col-6 {
  width: (6 / 12 * 100%);
}
.col-7 {
  width: (7 / 12 * 100%);
}
.col-8 {
  width: (8 / 12 * 100%);
}
.col-9 {
  width: (9 / 12 * 100%);
}
.col-10 {
  width: (10 / 12 * 100%);
}
.col-11 {
  width: (11 / 12 * 100%);
}
.col-12 {
  width: 100%;
}

@media (max-width: $breakpoint-lg) {
  .sm-width-full {
    width: 100% !important;
  }
}

.block {
  display: block !important;
}
.inline-block {
  display: inline-block !important;
}

.absolute {
  position: absolute;
}
.fixed {
  position: fixed;
}

.top-0 {
  top: 0;
}
.right-0 {
  right: 0;
}
.left-0 {
  left: 0;
}

.table {
  display: table !important;
}

.left {
  float: left;
}
.right {
  float: right;
}

// Responsive layout

@media (min-width: $breakpoint-lg) {
  .right-lg {
    float: right !important;
  }
  .left-lg {
    float: left !important;
  }

  .absolute-lg {
    position: absolute;
  }

  .block-lg {
    display: block !important;
  }
  .inline-block-lg {
    display: inline-block;
  }
}

@media (max-width: $breakpoint-lg) {
  .hide-sm {
    display: none !important;
  }
}

.clearfix:before,
.clearfix:after {
  content: " ";
  display: table;
}
.clearfix:after {
  clear: both;
}

.align-middle {
  vertical-align: middle;
}

/* Padding */
.px-0 {
  padding-left: 0;
  padding-right: 0;
}
.py-0 {
  padding-top: 0;
  padding-bottom: 0;
}

.px-1 {
  padding-left: $spacer-1;
  padding-right: $spacer-1;
}
.py-1 {
  padding-top: $spacer-1;
  padding-bottom: $spacer-1;
}

.px-2 {
  padding-left: $spacer-2;
  padding-right: $spacer-2;
}
.py-2 {
  padding-top: $spacer-2;
  padding-bottom: $spacer-2;
}

.px-3 {
  padding-left: $spacer-3;
  padding-right: $spacer-3;
}
.py-3 {
  padding-top: $spacer-3;
  padding-bottom: $spacer-3;
}

.px-4 {
  padding-left: $spacer-4;
  padding-right: $spacer-4;
}
.py-4 {
  padding-top: $spacer-4;
  padding-bottom: $spacer-4;
}

/* Margin */
.mx-auto {
  margin-left: auto;
  margin-right: auto;
}

.mt-0 {
  margin-top: 0;
}
.mr-0 {
  margin-right: 0;
}
.mb-0 {
  margin-bottom: 0;
}
.ml-0 {
  margin-left: 0;
}

.mt-1 {
  margin-top: $spacer-1;
}
.mr-1 {
  margin-right: $spacer-1;
}
.mb-1 {
  margin-bottom: $spacer-1;
}
.ml-1 {
  margin-left: $spacer-1;
}

.mt-2 {
  margin-top: $spacer-2;
}
.mr-2 {
  margin-right: $spacer-2;
}
.mb-2 {
  margin-bottom: $spacer-2;
}
.ml-2 {
  margin-left: $spacer-2;
}

.mt-3 {
  margin-top: $spacer-3;
}
.mr-3 {
  margin-right: $spacer-3;
}
.mb-3 {
  margin-bottom: $spacer-3;
}
.ml-3 {
  margin-left: $spacer-3;
}

.mt-4 {
  margin-top: $spacer-4;
}
.mr-4 {
  margin-right: $spacer-4;
}
.mb-4 {
  margin-bottom: $spacer-4;
}
.ml-4 {
  margin-left: $spacer-4;
}

// Responsive margin
@media (min-width: $breakpoint-lg) {
  .mx-lg-auto {
    margin-left: auto;
    margin-right: auto;
  }

  .mt-lg-0 {
    margin-top: 0;
  }
  .mr-lg-0 {
    margin-right: 0;
  }
  .mb-lg-0 {
    margin-bottom: 0;
  }
  .ml-lg-0 {
    margin-left: 0;
  }

  .mt-lg-1 {
    margin-top: $spacer-1;
  }
  .mr-lg-1 {
    margin-right: $spacer-1;
  }
  .mb-lg-1 {
    margin-bottom: $spacer-1;
  }
  .ml-lg-1 {
    margin-left: $spacer-1;
  }

  .mt-lg-2 {
    margin-top: $spacer-2;
  }
  .mr-lg-2 {
    margin-right: $spacer-2;
  }
  .mb-lg-2 {
    margin-bottom: $spacer-2;
  }
  .ml-lg-2 {
    margin-left: $spacer-2;
  }

  .mt-lg-3 {
    margin-top: $spacer-3;
  }
  .mr-lg-3 {
    margin-right: $spacer-3;
  }
  .mb-lg-3 {
    margin-bottom: $spacer-3;
  }
  .ml-lg-3 {
    margin-left: $spacer-3;
  }

  .mt-lg-4 {
    margin-top: $spacer-4;
  }
  .mr-lg-4 {
    margin-right: $spacer-4;
  }
  .mb-lg-4 {
    margin-bottom: $spacer-4;
  }
  .ml-lg-4 {
    margin-left: $spacer-4;
  }

  .lg-row {
    display: flex;
    flex-direction: row;
    align-items: center;
  }
  .new-line-on-mobile {
    br {
      display: none;
    }
  }
}

@media (max-width: $breakpoint-lg) {
  .ml-sm-2 {
    margin-left: $spacer-2;
  }
  .sm-column {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    .spacer {
      display: none;
    }
  }
  .new-line-on-mobile {
    line-break: strict;
  }
  .home-posts-feed {
    .link-primary {
      width: 100%;
    }
  }
}
